<template>
<div class="ue">
  <el-row :gutter="20">
    <el-col :span="24">
      <h2> - </h2>
      <u-editor v-model="value"></u-editor>
    </el-col>
    <el-col :span="24">
      <h2> -  toolbars</h2>
      <u-editor
        v-model="value"
        :config="config"></u-editor>
    </el-col>
    <el-col :span="12">
      <h2> - </h2>
      <u-editor
        v-model="value"
        disabled
        height="300px"></u-editor>
    </el-col>
    <el-col :span="12">
      <h2> - </h2>
      <u-editor
        v-model="value"
        disabled></u-editor>
    </el-col>
  </el-row>
</div>
</template>

<script>
import UEditor from 'plugin/UEditor'
export default {
  name: 'Ue',
  components: {UEditor},
  mixins: [],
  props: {},
  data () {
    return {
      value: '',
      config: {
        toolbars: [
          [
            'undo', // 
            'redo', // 
            '|',
            'fontfamily', // 
            'fontsize', // 
            'forecolor', // 
            'backcolor', // 
            'bold', // 
            'indent', // 
            'italic', // 
            'underline', // 
            'strikethrough', // 
            '|',
            'paragraph', // 
            'customstyle', // 
            'justifyleft', // 
            'justifyright', // 
            'justifycenter', // 
            'justifyjustify', // 
            '|',
            'rowspacingtop', // 
            'rowspacingbottom', // 
            'pagebreak', // 
            'lineheight', // 
            '|',
            'insertorderedlist', // 
            'insertunorderedlist', // 
            'subscript', // 
            'superscript', // 
            '|',
            'horizontal', // 
            'time', // 
            'date', // 
            'removeformat', // 
            'cleardoc', // 
            '|',
            'inserttable', // 
            'insertrow', // 
            'insertcol', // 
            'mergeright', // 
            'mergedown', // 
            'deleterow', // 
            'deletecol', // 
            'splittorows', // 
            'splittocols', // 
            'splittocells', // 
            'deletecaption', // 
            'inserttitle', // 
            'mergecells', // 
            'deletetable' // 
          ]
        ]
      }
    }
  },
  computed: {},
  watch: {},
  methods: {},
  created () {},
  mounted () {}
}
</script>

<style lang="less">
.ue {
  .u-editor {
    color: #000;
    .edui-default .edui-editor {
      // width: 100%!important;
    }
  }
}
</style>



