<template>
  <div class="editable-table">
    <pre><code>
  :

    1.  /  /  ; ,  / 

    2.    , 

    3. ,  ,   ;   
    </code></pre>
    <div class="button-group clearfix">
      <el-button type="primary"
        icon="el-icon-plus"
        style="float: left;"
        :disabled="!isFull"
        @click="addConfig">
        
      </el-button>
      <el-button type="primary"
        icon="el-icon-setting"
        style="float: right;"
        :disabled="!isFull || !isEdited"
        @click="saveConfig">
        
      </el-button>
    </div>
    <el-table :data="configList"
      border
      stripe
      @cell-click="cellClick"
      style="width:100%">
      <el-table-column align="center"
        prop="name"
        label=""
        min-width="120">
        <template slot-scope="scope">
          <el-input v-model.trim="scope.row.name"
            ref="inputName"
            v-if="scope.row.editName"></el-input>
          <span v-else>{{scope.row.name}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center"
        prop="type"
        label=""
        min-width="120">
        <template slot-scope="scope">
          <el-select v-model="scope.row.type"
            v-if="scope.row.editable && scope.row.editType"
            ref="inputType"
            @change="selectChange(scope.row)">
            <el-option v-for="item in typeOptions"
              :key="item.label"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span v-else>{{formatSelect(scope.row.type)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="value"
        label=""
        min-width="200">
        <template slot-scope="scope">
          <div v-if="scope.row.type !== undefined && scope.row.editValue">
            <el-input-number v-model="scope.row.value"
              ref="inputValue"
              v-if="scope.row.type === 0"
              controls-position="right"></el-input-number>
            <el-input v-model.trim="scope.row.value"
              ref="inputValue"
              v-if="scope.row.type === 1"></el-input>
            <el-checkbox v-model="scope.row.value"
              ref="inputValue"
              v-if="scope.row.type === 2">{{scope.row.value ? '' : ''}}</el-checkbox>
            <el-input v-model.trim="scope.row.value"
              ref="inputValue"
              v-if="scope.row.type === 3"
              type="textarea"
              autosize></el-input>
          </div>
          <div v-else>
            <span v-if="scope.row.type === 2">{{formatBoolean(scope.row.value)}}</span>
            <pre v-else-if="scope.row.type === 3"><code>{{scope.row.value}}</code></pre>
            <span v-else>{{scope.row.value}}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="describe"
        label=""
        min-width="200">
        <template slot-scope="scope">
          <el-input v-model="scope.row.describe"
            ref="inputDescribe"
            type="textarea"
            autosize
            v-if="scope.row.editDescribe"></el-input>
          <pre v-else-if="scope.row.type === 3"><code>{{scope.row.describe}}</code></pre>
          <span v-else>{{scope.row.describe}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center"
        label=""
        width="80">
        <template slot-scope="scope">
          <el-button type="text"
            class="el-icon-delete"
            @click="deleteConfig(scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pre><code>{{configList}}</code></pre>
  </div>
</template>

<script>
import { firstUpperCase } from 'utils'
export default {
  name: 'EditableTable',
  components: {},
  mixins: [],
  props: [],
  data () {
    return {
      configList: [],
      oldConfigList: [],
      typeOptions: [
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        },
        {
          value: 2,
          label: ''
        },
        {
          value: 3,
          label: 'JSON'
        }
      ],
      baseData: {
        name: '',
        type: '',
        value: '',
        describe: ''
      },
      baseEditable: {
        editName: false,
        editType: false,
        editValue: false,
        editDescribe: false
      }
    }
  },
  computed: {
    isFull () {
      return this.configList.every(item => item.name !== '' && item.type !== '' && item.value !== '')
    },
    isEdited () {
      if (this.oldConfigList.length === this.configList.length) {
        let flag = false
        this.oldConfigList.forEach((item, index) => {
          if (!flag) {
            for (const key in this.baseData) {
              if (this.baseData.hasOwnProperty(key)) {
                if (item[key] !== this.configList[index][key]) {
                  flag = true
                  break
                }
              }
            }
          } else {
            return true
          }
        })
        return flag
      } else {
        return true
      }
    }
  },
  watch: {},
  methods: {
    // id   id
    generateId () {
      return Date.now() + '' + parseInt(Math.random() * 1000)
    },
    // 
    fetchConfigList () {
      // 
      let res = {
        data: [
          {
            id: '1578137133402826',
            name: '',
            type: 1,
            value: 'lhajh',
            describe: '^_^'
          },
          {
            id: '1578137133402827',
            name: '',
            type: 0,
            value: 27,
            describe: 'qaq'
          },
          {
            id: '1578137133402828',
            name: '',
            type: 2,
            value: true,
            describe: '? '
          },
          {
            id: '1578137133402829',
            name: '',
            type: 3,
            value: '{\n  "value": 0,\n  "label": "",\n  "value": 1,\n  "label": "",\n  "value": 2,\n  "label": "",\n  "value": 3,\n  "label": "JSON",\n}',
            describe: ''
          }
        ]
      }
      this.formatConfig(res.data)
    },
    // 
    addConfig () {
      let row = {
        ...this.baseData,
        ...this.baseEditable,
        editable: true
      }
      this.configList.push(row)
      this.cellClick(row, { property: 'name' })
    },
    // 
    saveConfig () {
      // ,  fetchConfigList
      this.formatConfig(this.configList)
      this.$message.success('')
    },
    // 
    deleteConfig (row) {
      // ,  fetchConfigList
      //  id, , 
      if (row.id) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(_ => {
          // , , ; 
          if (this.isEdited) {
            this.configList.splice(this.configList.indexOf(row), 1)
          } else {
            this.configList.splice(this.configList.indexOf(row), 1)
            this.formatConfig(this.configList)
          }
          this.$message.success('')
        })
        //  id, 
      } else {
        this.configList.splice(this.configList.indexOf(row), 1)
      }
    },
    // 
    cellClick (row, event, collumn) {
      if (event.label !== '') {
        //   html  .stop :
        // Error in event handler for "cell-click": "TypeError: $event.stopPropagation is not a function"
        window.event.stopPropagation()
        let property = firstUpperCase(event.property)
        this.disabledEdit()
        //  vaule , , 
        if (property === 'Value' && row.type === '') {
          this.$message.warning('')
          return
        }
        row[`edit${property}`] = true
        this.$nextTick(() => {
          this.$refs[`input${property}`] &&
            typeof this.$refs[`input${property}`].focus === 'function' &&
            this.$refs[`input${property}`].focus()
        })
      }
    },
    // 
    disabledEdit () {
      this.configList.forEach(item => {
        item = Object.assign(item, this.baseEditable)
      })
    },
    // 
    selectChange (row) {
      switch (row.type) {
        case 0:
          row.value = 0
          break
        case 2:
          row.value = false
          break
        default:
          row.value = ''
          break
      }
      //  change , :
      // Error in nextTick: "TypeError: Cannot read property 'focus' of undefined"
      this.$nextTick(() => {
        row.editType = false
        this.cellClick(row, { property: 'value' })
      })
    },
    // 
    formatConfig (val) {
      this.configList = val.map(item => {
        return {
          ...item,
          ...this.baseEditable,
          editable: false,
          id: item.id || this.generateId()
        }
      })
      this.oldConfigList = val
    },
    formatSelect (val) {
      return this.typeOptions.reduce((acc, item) => {
        return item.value === val ? acc + item.label : acc
      }, '')
    },
    formatBoolean (val) {
      if (val == null || val === '') {
        return ''
      } else {
        return val ? '' : ''
      }
    }
  },
  created () {},
  mounted () {
    this.fetchConfigList()
    document.body.addEventListener('click', this.disabledEdit)
  },
  beforeDestroy () {
    document.body.removeEventListener('click', this.disabledEdit)
  }
}
</script>

<style lang="less">
.editable-table {
  .button-group {
    margin-bottom: 20px;
  }
  .el-table {
    margin-bottom: 20px;
    .el-table__row {
      .cell {
        cursor: pointer;
      }
    }
    .el-input-number {
      width: 100%;
    }
  }
}
</style>



